<template>
  <v-app dark>
    <v-app-bar
      app
      height="100"
      color="deep-purple darken-4"
      dark
      class="display-2"
    >
      <span class="font-weight-light">COVID-19</span>
      <v-spacer></v-spacer>
      <span right>vaccinationdata version 0.5</span>
    </v-app-bar>
    <v-main>
      <v-container class="my-2">
        <v-layout row wrap>
          <v-flex xs12 sm6 md4 lg4>
            <v-card class="ma-3">
              <apexchart type="bar" :options="options1" :series="series"></apexchart>
            </v-card>
            <v-card class="ma-3">
              <apexchart type="bar" :options="options" :series="herstellerDaten"></apexchart>
            </v-card>
          </v-flex>
          <v-flex xs6 sm3 md8 lg8 >
            <v-card class="ma-3">
              <apexchart type="area" :options="chartOptions" :series="newseries"></apexchart>
            </v-card>
            <v-card>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import axios from 'axios'


export default {
  name: 'App',
  data () {
    return {
      dataTableStates: null,
      dataTest: null,
      biontechData: null,
      modernaData: null,
      historyDates: [],
      allDates: [],
      sortedDates: [],
      sortedVccinated: [],
      sortedSecondVccinated: [],
      finalDates: [],
      options1: {
        chart: {
          toolbar: {
            show: false,
          },
        },
        xaxis: {
          categories: ['Nur eine Impfung', 'Beide Impfungen'],
        },
        yaxis: {
          show: false
        },
        grid: {
          show: false
        },
      },
      options: {
        chart: {
          toolbar: {
            show: false
          }
        },
        xaxis: {
          categories: ['Biontech', 'Moderna']
        },
        yaxis: {
          show: false
        },
        grid: {
          show: false
        }
      },
      chartOptions: {
            chart: {
              toolbar: {
                show: false
              },
              height: 350,
              type: 'line',
              zoom: {
                enabled: false
              }
            },
            dataLabels: {
              enabled: false
            },
            stroke: {
              curve: 'smooth'
            },
            title: {
              text: 'Impfungen nach Tagen',
              align: 'center',
              margin: 10,
            },
            grid: {
              row: {
                colors: ['#f3f3f3', 'transparent'],
                opacity: 0.5
              },
            },
            xaxis: {
              type: 'datetime',
              categories: [ "2020-12-27T00:00:00.000Z", "2020-12-28T00:00:00.000Z", "2020-12-29T00:00:00.000Z", "2020-12-30T00:00:00.000Z", "2020-12-31T00:00:00.000Z", "2021-01-01T00:00:00.000Z", "2021-01-02T00:00:00.000Z", "2021-01-03T00:00:00.000Z", "2021-01-04T00:00:00.000Z", "2021-01-05T00:00:00.000Z", "2021-01-06T00:00:00.000Z", "2021-01-07T00:00:00.000Z", "2021-01-08T00:00:00.000Z", "2021-01-09T00:00:00.000Z", "2021-01-10T00:00:00.000Z", "2021-01-11T00:00:00.000Z", "2021-01-12T00:00:00.000Z", "2021-01-13T00:00:00.000Z", "2021-01-14T00:00:00.000Z", "2021-01-15T00:00:00.000Z", "2021-01-16T00:00:00.000Z", "2021-01-17T00:00:00.000Z", "2021-01-18T00:00:00.000Z", "2021-01-19T00:00:00.000Z", "2021-01-20T00:00:00.000Z", "2021-01-21T00:00:00.000Z", "2021-01-22T00:00:00.000Z", "2021-01-23T00:00:00.000Z", "2021-01-24T00:00:00.000Z", "2021-01-25T00:00:00.000Z", "2021-01-26T00:00:00.000Z", "2021-01-27T00:00:00.000Z", "2021-01-28T00:00:00.000Z", "2021-01-29T00:00:00.000Z", "2021-01-30T00:00:00.000Z", "2021-01-31T00:00:00.000Z", "2021-02-01T00:00:00.000Z", "2021-02-02T00:00:00.000Z", "2021-02-03T00:00:00.000Z", "2021-02-04T00:00:00.000Z", "2021-02-05T00:00:00.000Z" ],
            },
            colors: ['#22BFEA','#E05C3F']
          },
          tooltip: {
            x: {
              format: 'dd/MM/yy'
            }
          },
    }
  },
  components: {
    
  },
  computed: {
    series: function() {
      return [{
        name: 'Impfungen',
        data: [this.dataTableStates, this.dataTest],
      }]
    },
    herstellerDaten: function() {
      return [{
        data: [this.biontechData, this. modernaData]
      }]
    },
    newseries: function(){
      return [{
        name: 'Erste Impfung',
        data: this.sortedVccinated 
      },
      { name: 'Zweite Impfung',
        data: this.sortedSecondVccinated }]
    }
  },
  methods: {
    async getVaccinationData(){
        const urlForVaccinations = 'https://api.corona-zahlen.org/vaccinations'
        await axios
        .get(urlForVaccinations)
            .then(response => (
          this.dataTableStates = response.data.data.vaccinated,
          this.dataTest = response.data.data.administeredVaccinations,
          this.biontechData = response.data.data.vaccination.biontech,
          this.modernaData = response.data.data.vaccination.moderna
          ))
    },
    async getVaccinationDataHistory(){
      const urlForVaccinationsHistory = 'https://api.corona-zahlen.org/vaccinations/history'
      await axios
      .get(urlForVaccinationsHistory)
        .then(response2 => (
          this.historyDates = response2.data.data.history,
          this.allDates = this.historyDates.map( a => a.date),
          this.finalDates = this.allDates.map( d => new Date(d)),

          this.historyVaccinated = this.historyDates.map( v => v.vaccinated),
          this.historyVaccinated.forEach(vaccin => {
            this.sortedVccinated.push(vaccin)
          }),
          this.historyVaccinatedSeondVac = this.historyDates.map( secondV => secondV.secondVaccination),
          this.historyVaccinatedSeondVac.forEach(secVac => {
          this.sortedSecondVccinated.push(secVac)
          })
        ))
    },
  },
  mounted () {
    this.getVaccinationData()
    this.getVaccinationDataHistory()
  },
}
</script>
